.section-store-list {
	.section-content-inner {
		overflow: hidden;
	}
	.section-heading-wrapper {
		p {
			font-size: 13px;
			color: var(--subtext-color); 
		}
	}
	.store-item {
		padding-bottom: 40px;
		margin-bottom: 40px;
	}
	.store-title {
		font-size: 17px;
		line-height: 1.42em;
		font-weight: 500;
	}
	.store-address {
		p {
			font-weight: 500;
		}
	}
	.store-working-time {
		font-size: 13px;
		color: var(--subtext-color);
		>* {
			line-height: 1.47em;
		}
	}
	.store-phone-number {
		font-size: 20px;
		margin: 20px 0;
		font-weight: 500;
	}
	@media (min-width: 750px) {
		.store-item {
			padding-bottom: 70px;
			margin-bottom: 70px;
		}
	}
}